@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
body {
  background-color: rgba(106, 142, 35, 0.144);
  text-align: center;
  margin: 0;
  padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button,
body {
  font-family: 'Luckiest Guy', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
footer {
  color: olive;

  text-align: left;
  padding: 0.1% 0.1% 0.1% 0.5%;
  font-size: 70%;
  background-color: rgba(106, 142, 35, 0.253);
}
header {
  display: -webkit-flex;
  display: flex;
  background-color: rgba(106, 142, 35, 0.253);
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 1% 0 1%;
}
h1 {
  color: olive;
}
h2 {
  font-size: 500%;
  margin-top: 0;
}
button {
  color: olive;
  border: 5px solid olive;
  background-color: rgba(106, 142, 35, 0.253);
  border-radius: 15px;
  font-size: 150%;
  padding: 1%;
}
button:hover {
  color: rgb(172, 172, 2);
  border-color: rgb(172, 172, 2);
}
.infoCon {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.infoCon .score {
  font-size: 100%;
}
.score {
  font-size: 300%;
  color: goldenrod;
}
.net {
  background-color: whitesmoke;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 850px;
  border-style: solid;
  border-width: 7px;
  padding: 12px 16.5px 13px 16.5px;
  border-image: url(/Snake/static/media/frame.ddc091de.png) 10 fill round;
}

@media only screen and (max-width: 850px) {
  header {
    padding: 0 5% 0 5%;
  }
  p {
    padding: 0 6% 65% 6%;
  }
}

